import React, {Component} from 'react';

import Aux from '../../hoc/Auxe/Aux';
import Burger from '../../components/Burger/Burger';
import BuildControls from '../../components/Burger/BuildControls/BuildControls';
import Modal from '../../components/UI/Modal/modal';
import OrderSummary from '../../components/Burger/OrderSummary/orderSummery';
import Spinner from '../../components/UI/Spinner/Spinner';
import withErrorHandler from '../../hoc/withErrorHandler/withErroHandler';
import axios from '../../axios-orders';


const INGRIDIENT_PRICES={
    salad: 0.5,
    cheese: 0.4,
    meat: 1.3,
    bacon: 0.7
};

class BurgerBuilder extends Component{
    //constructor(props){
      //  super(props);
        //this.state{...}
    //}
    state = {
        ingridients: {
            salad: 0,
            bacon: 0,
            cheese: 0,
            meat: 0
        },
        totalPrice: 4,
        purchaseable: false,
        purchasing: false,
        loading: false
    }

    updatePurchaseState (ingridients) {
        const sum = Object.keys(ingridients)
        .map(igKey =>{
            return ingridients[igKey]
        })
        .reduce((sum, el) => {
            return sum + el;
        }, 0);
    this.setState({purchaseable: sum > 0 });
    } 

    addIngridientHandler = (type)=>{
        const oldCount = this.state.ingridients[type];
        const updatedCount = oldCount + 1;
        const  updatedIngridients = {
            ...this.state.ingridients
        };
        updatedIngridients[type] = updatedCount;
        const priceAddition = INGRIDIENT_PRICES[type];
        const OldPrice =this.state.totalPrice;
        const newPrice = OldPrice + priceAddition;
        this.setState({totalPrice: newPrice, ingridients: updatedIngridients});
        this.updatePurchaseState(updatedIngridients); 

    }

    removeIngridientHandler=(type)=>{
        const oldCount = this.state.ingridients[type];
        if ( oldCount <= 0){
            return;
        }
        const updatedCount = oldCount - 1;
        const  updatedIngridients = {
            ...this.state.ingridients
        };
        updatedIngridients[type] = updatedCount;
        const priceDeduction = INGRIDIENT_PRICES[type];
        const OldPrice =this.state.totalPrice;
        const newPrice = OldPrice - priceDeduction;
        this.setState({totalPrice: newPrice, ingridients: updatedIngridients});
        this.updatePurchaseState(updatedIngridients);
       

    }

    purchaseHandler=()=>{
        this.setState({purchasing: true})
    }

    purchaseCancelHandler = () =>{
        this.setState({purchasing: false});
    }

    purchaseContinueHandler = ()=>{
        //alert('You continue!');
        this.setState({ loading: true });
        const order = {
            ingredients: this.state.ingridients,
            price: this.state.totalPrice,
            customer:{
                name: 'Shokhrukhmirzo',
                address:{
                    street: 'Naqshbandiy 8',
                    zipCode:'43212',
                    country: 'Uzbekistan'
                },
                email: 'a.bellamy.@inbox.ru'
            },
            deliveryMethod: 'fastest'

        }

        axios.post('/orders.json', order)
        .then(response=> {
            this.setState({ loading:false, purchasing: false });
        })
        .catch(error => {
            this.setState({ loading: false, purchasing: false });
        });
        
    }

    render(){
        const disabledInfo = {
            ...this.state.ingridients
        };
        for (let key in disabledInfo){
            disabledInfo[key] = disabledInfo[key] <= 0
        }
        let orderSummary =  <OrderSummary 
        ingridients={this.state.ingridients}
        price={this.state.totalPrice}
        purchaseCancelled={this.purchaseCancelHandler}
        purchaseContinued={this.purchaseContinueHandler}/>;
        if(this.state.loading){
            orderSummary = <Spinner/>

        }
        //{salad: true,meat,: false, ... }
        return (
            <Aux>
                <Modal show={this.state.purchasing} modalClosed={this.purchaseCancelHandler}>
                   {orderSummary}
                </Modal>
                <Burger ingridients={this.state.ingridients}/>
                <BuildControls ingridientAdded={this.addIngridientHandler}
                               ingridientRemoved={this.removeIngridientHandler}
                               disabled={disabledInfo}
                               purchaseable={this.state.purchaseable}
                               ordered={this.purchaseHandler}
                               price={this.state.totalPrice}/>
            </Aux>
        );
    }

}

export default withErrorHandler(BurgerBuilder, axios);