import React from 'react';

import classes from './Burger.css';
import Ingridient from './Ingridients/ingridient';

const burger = (props) =>{
    let transformedIngridients = Object.keys(props.ingridients)
    .map(igKey => {
        return [...Array(props.ingridients[igKey])].map((_, i)=>{
           return <Ingridient key={igKey + i} type={igKey}/>
        });
    })
    .reduce((arr,el)=> {
        return arr.concat(el)
    }, []);

    if (transformedIngridients.length === 0){
        transformedIngridients = <p>Please start adding ingridients!</p>;
    }

    return(
        <div className={classes.Burger}>
        <Ingridient type="bread-top"/>
        {transformedIngridients}
        <Ingridient type="bread-bottom"/>
        </div>
    );
};

export default burger;